import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
class LaporanServiceAmbilExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Service Ambil"
          sheet="Laporan Service Ambil"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="5" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN SERVICE AMBIL{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="5"> Periode </th>
            </tr>
            <tr>
              <th colSpan="5">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="5"> TOKO : {getUserdata().nama_toko} </th>
            </tr>

            <tr>
              <th colSpan="5"> ALAMAT : {getUserdata().alamat_toko} </th>
            </tr>
            <tr></tr>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO SERVICE
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TGL TRANSAKSI
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA CUSTOME
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                ALAMAT
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                SALES
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                DESKRIPSI
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                BERAT
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                ONGKOS
              </td>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((row, index) => (
              <>
                <tr key={index}>
                  <td>{row.no_faktur_service}</td>
                  <td>{row.tgl_system}</td>
                  <td>{row.nama_customer}</td>
                  <td>{row.alamat_customer}</td>
                  <td>{row.kode_sales}</td>
                  <td>{row.detail_barang.deskripsi}</td>
                  <td>{row.detail_barang.berat.toFixed(2)}</td>
                  <td>{row.detail_barang.ongkos.toLocaleString("kr-KO")}</td>
                </tr>
              </>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
                colSpan="8"
              >
                Total Transaksi {this.props.data.length}
              </td>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanServiceAmbilExel;
